.sales-voucher-container, .purchase-voucher-container {
  margin-left: 19%;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding: 10px;
  border: 1px solid #ccc;
  background: linear-gradient(to right, #bceee8, #eff1c6);
  border-radius: 8px;
  margin-bottom: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.header {
  margin-bottom: 10px;
}

.header-row {
  display: flex;
  justify-content: space-between;
}

.aligned-item {
  display: flex;
  font-size: 14px;
}

.aligned-item label {
  padding: 5px;
  padding-right: 2px;
}

.aligned-item input,
.aligned-item select {
  height: 30px;
  font-size: 14px;
  width: 110px;
}

.aligned-item select {
  height: 30px;
  font-size: 14px;
  width: 110px;
  padding: 0px;
}

.party-section {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  font-size: 14px;
}

.party-section label {
  padding: 5px;
}

.party-section input {
  width: 41.8%;
}

.narration-section {
  display: flex;
  justify-content: left;
  margin-bottom: 10px;
}

.narration-section input {
  width: 100%;
}

.narration-section label {
  padding: 5px;
  font-size: 14px;
}

.table-section {
  margin-bottom: 10px;
  overflow-y: auto;
  background-color: white;
}

.table-section table {
  width: 100%;
  border-collapse: collapse;
}

.table-section th,
.table-section td {
  border: 1px solid #000;
  text-align: left;
  padding: 5px;
}

.table-section th {
  background-color: #d3d3d3;
}

.table-section td input {
  width: 100%;
  border: none;
  outline: none;
  padding: 5px;
  font-size: 14px;
}

.table-section td input:focus {
  border: none;
}

.totals-section {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.gst-section {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.gst-summary,
.bill-sundry {
  width: 49%;
}

.gst-summary h4,
.bill-sundry h4 {
  margin: 0;
  text-align: left;
}

.gst-summary table,
.bill-sundry table {
  width: 100%;
  border-collapse: collapse;
  background-color: white;

}

.gst-summary th,
.gst-summary td,
.bill-sundry th,
.bill-sundry td {
  border: 1px solid #000;
  text-align: left;
  padding: 5px;
}

.gst-summary td {
  border: 1px solid #000;
  text-align: left;
  padding: 5px;
  height: 150px;
}

.gst-summary th,
.bill-sundry th {
  border: 1px solid #000;
  text-align: left;
  padding: 5px;
  background-color: #d3d3d3;
}

.gst-summary td input,
.bill-sundry td input {
  width: 100%;
  border: none;
  outline: none;
  padding: 5px;
  text-align: right;
}

.gst-summary td input:focus,
.bill-sundry td input:focus {
  border: none;
}

/* Limiting table rows with scroll functionality */
.table-section table tbody {
  display: block;
  max-height: 420px; /* Adjust based on the desired height */
  overflow-y: scroll;
}

.table-section thead {
  display: table;
  width: 98.2%;
  table-layout: fixed;
}
 
.table-section tbody tr {
  display: table;
  width: 100%;
  table-layout: fixed;
}

/* Adding scroll functionality to the Bill Sundry table */
.bill-sundry table tbody {
  display: block;
  max-height: 210px; /* Adjust to show only 5 rows */
  overflow-y: scroll;
}

.bill-sundry thead {
  display: table;
  width: 96.2%;
  table-layout: fixed;
}

.bill-sundry tbody tr {
  display: table;
  width: 100%;
  table-layout: fixed;
}


.hidden-row {
  display: none;
}

.footer {
  display: flex;
  justify-content: space-between;
}

.footer button {
  padding: 5px;
  margin-right: 5px;
  font-size: 14px;
}
.table-section table {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed; /* This ensures fixed column widths */
}

.table-section th,
.table-section td {
  border: 1px solid #000;
  text-align: left;
  padding: 5px;
}

/* Set column widths */
.table-section th:nth-child(1), 
.table-section td:nth-child(1) {
  width: 5%; /* Adjust as needed */
}

.table-section th:nth-child(2), 
.table-section td:nth-child(2) {
  width: 30%; /* Adjust as needed */
}

.table-section th:nth-child(3), 
.table-section td:nth-child(3) {
  width: 8%; /* Adjust as needed */
}

.table-section th:nth-child(4), 
.table-section td:nth-child(4) {
  width: 8%; /* Adjust as needed */
}

.table-section th:nth-child(5), 
.table-section td:nth-child(5) {
  width: 10%; /* Adjust as needed */
}

.table-section th:nth-child(6), 
.table-section td:nth-child(6) {
  width: 10%; /* Adjust as needed */
}

/* Set column widths */
.bill-sundry th:nth-child(1), 
.bill-sundry td:nth-child(1) {
  width: 6%; /* Adjust as needed */
}

.bill-sundry th:nth-child(2), 
.bill-sundry td:nth-child(2) {
  width: 20%; /* Adjust as needed */
}

.bill-sundry th:nth-child(3), 
.bill-sundry td:nth-child(3) {
  width: 7%; /* Adjust as needed */
}

.bill-sundry th:nth-child(4), 
.bill-sundry td:nth-child(4) {
  width: 12%; /* Adjust as needed */
}

.total-gst {
  display: flex;
  margin-top: 10px;
  padding: 5px;
  padding-right: 0px;
  font-weight: bold;
}

.total-gst label {
  padding: 5px;
}

.total-gst input {
  border: 1px solid black;
  margin-left: 5px;
  border-radius: 0px;
  text-align: right;
}

