/* Media Queries for Responsiveness */

@media (max-width: 600px) {
.content-section {
    flex-direction: column;
    align-items: center;
}
.photo-section{
  margin: auto;
}
.form-group {
    width: 100%;
}
.form-row input ,.form-row select{
  width: 95%;
}
.form-box {
    margin: 0;
    width: 90%;
}
.photo-section {
    margin-bottom: 20px;
}
}


@media (min-width: 600px) and (max-width: 900px) {
.form-group {
    width: 48%;
}

.form-box {
    width: 80%;
    margin-right: 30px;
}
.photo-section{
  margin-right: 30px;
}
}

